.App {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#root {
  width: 100%;
}

.activeUser {
  animation: bgColor infinite;
}

@-webkit-keyframes bgColor {
  0%,
  49% {
    background-color: yellow;
    border: 3px solid red;
  }
  50%,
  100% {
    background-color: white;
    border: none;
  }
}
